.card-container {
  height: 30vh;
  width: 70vh;
  background-color: rgb(190, 117, 20);
  border-radius: 16px;
  margin: 30px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 20px;
  margin-left: 150px;
}
@media screen and (max-width: 468px) {
  .card-container {
    height: 30vh;
    width: 30vh;
    background-color: rgb(204, 126, 24);
    border-radius: 16px;
    margin: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: space-between;
    padding: 20px;
  }
}
